const baseUrl = "https://api.gainiit.com";

// get information of student start
const token = localStorage.getItem('token');
const requestOptions2 = {
  method: 'GET',
  mode: "cors",
  headers: { 
    'Content-Type': 'application/json',
    'Authorization': `Token ${token}`,
  }
}
fetch(`${baseUrl}/api/v1/auth/profile/`, requestOptions2)
.then((response) => response.json())
.then((data) => {
  localStorage.setItem('batch_name', data.batch_name);
  localStorage.setItem('batch', data.batch);
  localStorage.setItem('fullName', data.fullName);
  localStorage.setItem('id', data.id);
})
.catch(error => console.log("Error detected: " + error))

// get information of student end


export const requestOptions = {
    method: 'GET',
    mode: "cors",
    headers: { 'Content-Type': 'application/json' }
};


export const Mytime = (value) =>{
    const currentDate = new Date(value)
       return currentDate.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
  }
export const Mydate = (value) =>{
    const currentDate = new Date(value)
       return currentDate.toDateString()
  }

